@import '../../assets/styles/variables';
@import url('https://fonts.googleapis.com/css?family=Oswald&display=swap');

.header {
  width: 100%;
  background-color: $mainYellow;
  display: flex;
  justify-content: center;
  align-items: center;
  height: auto;
  
  &-wrapper {
    display: flex;
    width: 100%;
    justify-content: space-between;
    height: 50px;
  }
  
  &-title {
    display: flex;
    width: auto;
    align-items: center;

    &__text {
      color: $darkFont;
      margin-left: 80px;
      font-weight: 900;
      font-size: 2.2em;
      font-family: 'Oswald';
      text-transform: uppercase;
      letter-spacing: 3px;
    }

    &-logo {
      width: 40px;
      height: 40px;
      object-fit: cover;

      &__img {
        height: 45px;
        &:hover {
          cursor: pointer;
        }
      }
    }
  }

  &-navs {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 400px;

    &__link {
      color: $darkFont;
      font-weight: bold;
      font-size: 0.8em;

      &:hover {
       cursor: pointer;
      }

      &_resume {
        padding: 5px 15px;
        border-radius: 5px;
        //color: $mainYellow;
        //background-color: $gradiemtEnd;
        border: solid 2px $gradiemtEnd;
      }
    }
  }
}